
header nav a {
  color: white;
  text-decoration: none;
  padding: 10px;
  opacity: 0.8;
  cursor: progress;
  font-size: 12px;
  font-family: monospace;
  
}

header nav {
  display: flex;
  margin-top: 8px;
}
a:hover {
  opacity: 1;
  transform: scale(1.2);
  transition: 0.2s;
}
a:visited,
a:hover,
a:active {
  color: honeydew;
}

.sobre-mim {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  padding-top: 80px;
}

.sobre-mim img {
  border-radius: 50%;
}

.sobre-mim p {
  width: 40%;
  font-size: 20px;
  text-align: center;
  align-self: center;
}

.soft-skills {
  margin: 0px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding-bottom: 50px;
}
.soft-skills ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  list-style: none;
  text-align: center;
}

 .soft-skills li {
  list-style: none;
  text-align: center;
}

.soft-skills h1 {
  font-size: 40px;
  margin-top: 200px;
  text-align: center;
}

.hobbies-containers h1 {
  display: flex;
  font-size: 40px;
  margin-top: 100px;
  justify-content: center;
}

.Hobbies {
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding-bottom: 200px;
}

.Hobbies img {
  margin-top: 100px;
  width: 180px;
  height: 160px;
  box-shadow: 5px 5px 15px 5px rgb(0, 255, 0);
}

.Hobbies img:hover {
  transform: scale(1.2);
  transition: 0.6s;
}

.button-react {
  margin-top: 6px;

}


.my-projetos h1 {
  text-align: center;
  font-size: 40px;
}


.img-projetos {
  display: flex;
  justify-content: space-around;
  padding-bottom: 50px;
}

.img-projetos img {
  width: 200px;
  margin-top: 100px;
  box-shadow: 5px 5px 15px 5px rgb(0, 255, 0);
}

.img-projetos img:hover {
  transform: scale(1.2);
  transition: 0.6s;
}

.projetos-nav {
  display: flex;
  flex-flow: column;
}

.projetos-nav a:hover {
  background-color: rgb(32,32,34);
  color: white;
}

.projetos-nav a {
  margin: 0 auto;
  color: black;
  margin-top: 30px;
  text-align: center;
  text-decoration: none;
  background-color: rgb(0, 255, 0);
  border-radius: 3px;
  width: 90px;
  padding: 5px;
}

.videos h1 {
  width: 100%;
  font-size: 40px;
  text-align: center;
  margin-top: 100px;
}
.videos {
  padding-bottom: 200px;
}

.iframe1 {
  display: flex;
  justify-content: space-around;
}

.iframe1 video {
  margin-top: 100px;
  width: 401px;
  height: 301px;
  object-fit: cover;
}

.incon {
  display: flex;
  justify-content: center;
  flex-direction: row-reverse;
  padding-bottom: 20px;
}

.redes {
  display: flex;
  justify-content: center;
  font-size: 40px;
}

.redes a {
  margin: 10px;
  color: black;
  margin-top: 30px;
  border-radius: 3px;
}

.redes a:hover {
  background-color: rgb(32, 34, 34);
}


.fab {
  color:rgb(0, 255, 0);
}

@media screen and (max-width: 600px) {
  header {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  header nav {
    display: none;
  }
  .sobre-mim {
    display: flex;
    flex-flow: column;
    align-items: center;
  }
  .sobre-mim P {
    width: 90%;
    font-size: 15px;
    margin-top: 10px;
  }
  .soft-skills {
    width: 100%;
    display: flex;
    flex-flow: wrap column;
  }
  .soft-skills h1 {
    text-align: center;
    margin: 40px;
  }

  .Hobbies {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
  }

  .hobbies-containers h1 {
    margin-bottom: 40px;
  }

  .Hobbies img {
    margin: 20px auto;
    box-shadow: 2px 2px 2px 2px rgb(5, 255, 5);
  }

  .my-projetos {
    width: 100%;
    margin-top: 0px auto;
    padding-bottom: 30px;
  }

  .my-projetos h1 {
    margin-bottom: 40px;
  }

  .img-projetos {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin: 0px;
  }

  .iframe1 {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  .iframe1 video {
    margin-top: 100px;
    width: 300px;
    height: 300px;
    object-fit: cover;
  }
}

